.app-name-section{
  background-color: #FB5B16;
  height: 10vh;
  width: 100vw; 
  color: white;
  padding:10px 20px 10px 20px;
}

h1,h5,h6{
  font-family: 'Roboto', sans-serif;
}

@media screen and (min-width: 601px) {
  .app-name-section {
    font-size: 18px;
  }
}

@media screen and (max-width: 600px) {
  .app-name-section {
    font-size: 13px;
  }
}
.image{
    max-width: 100%;
    height: auto;
}

.coding1-image{
  display:inline;
  float: right;
  margin-right: 5%;
}

.text-orange{
  color: #FB5B16;
}

.mybtn {
  font-family: 'Roboto';
  color: #ffffff;
  font-size: 20px;
  background: #FB5B16;
  padding: 5px 30px 5px 30px;
  text-decoration: none;
  border: none;
  border-radius: 14px;
  margin-left: 15%;
  margin-top: 1%;
}

.firstpage-text{
  margin-left: 20%;
  margin-top: 20%;
}
#header-first-page{
  margin-left: 15%;
  margin-top: 5%;
}
#p-first-page{
  margin-left: 15%;
}
#second-p-first-page{
  margin-left: 15%;
}
.centered-text{
  text-align: center;
}
.backgroundOfFirstPage{
  background: #EEE7E7;
}
#block-text{
  display: block;
  margin-top: 13%;
}


.btn-second-page{
  margin-top: 20%;
