}

.round-btn {
  border-radius: 50%;
  font-family: Arial;
  color: #ffffff;
  border: none;
  background: #590BB2;
  padding: 15px 20px 15px 20px;
  text-decoration: none;
}

.last-page-text{
  color:#373365;
}
.card-text{
